export default {
	data: {
		// AudioContext
		audio_context: null,
		// Recorder
		recorder: null
	},
	// --------------- begin---------------
	setData: function(key, value){
		this.data[key]=value;
	},
	getData: function(key){
		return this.data[key];
	},
	// --------------- end---------------

	// 
	// flagtrue count
	getDate: function(flag, count){
		function p(s) {
		          return s < 10 ? '0' + s: s;
		}
		var myDate = new Date();
		var year=null;
		var month=null;
		var date=null;
		var h=null;
		var m=null;
		var s=null;
		if(flag){
			//
			year=myDate.getFullYear();
			//
			month=myDate.getMonth()+1;
			//
			date=myDate.getDate();
			h=myDate.getHours();       //(0-23)
			m=myDate.getMinutes();     //(0-59)
			s=myDate.getSeconds();
		}else{
			date=myDate.getDate();
			myDate.setDate(date+count);
			//
			year=myDate.getFullYear();
			//
			month=myDate.getMonth()+1;
			//
			date=myDate.getDate();
		}
		var now=year+'-'+p(month)+"-"+p(date);
		return now;
	},

	// 
	scrollTo: function(value){
		var top=document.body.scrollTop;
		var timer=null;
		timer=setInterval(function(){
			if(top<value){
				top+=10;
				window.scrollTo(0, top);
				if(top>=value){
					clearInterval(timer);
					timer=null;
				}
			}else{
				top-=10;
				window.scrollTo(0, top);
				if(top<=value){
					clearInterval(timer);
					timer=null;
				}
			}
		},1);
	},

	// --------------DOMbegin--------------
	setSessionData: function(key, value){
		// 
		var val='';
		if(value!=null){
			val=JSON.stringify(value);
		}
		if(key!=''&&val!=''){
			window.sessionStorage.setItem(key, val);
		}
	},
	getSessionData: function(key){
		return JSON.parse(window.sessionStorage.getItem(key));
	},
	removeSessionData: function(key){
		window.sessionStorage.removeItem(key);
	},
	// --------------DOMend--------------

	// --------------begin--------------
	audioInit: function(){
		var self=this;
		try {
			window.AudioContext = window.AudioContext || window.webkitAudioContext;
			navigator.mediaDevices.getUserMedia = navigator.mediaDevices.getUserMedia || navigator.mediaDevices.webkitGetUserMedia;
			window.URL = window.URL || window.webkitURL;
			this.data.audio_context = new AudioContext;
		} catch (e) {
			alert('No web audio support in this browser!');
		}

		navigator.mediaDevices.getUserMedia({audio: true}, self.startUserMedia, function(e) {});
	},
	startUserMedia: function(stream){
		console.log(1);
		var input = this.data.audio_context.createMediaStreamSource(stream);
		this.data.recorder = new Recorder(input);
		console.log(this.data.recorder);
	}
	// --------------end--------------
}


